<template>
    <div>
        <!--        进度条-->
        <div class="progress">
            <div class="progress-bar progress-bar-striped bg-warning" role="progressbar"
                 v-bind:style="{width: (message*100/5)+'%'}" aria-valuenow="10" aria-valuemin="0" aria-valuemax="100">
            </div>
        </div>

        <div class="jumbotron jumbotron-fluid mt-3">
            <div class="container">
                <h1 class="display-4" >三，下列哪些是动态网页技术？</h1>

                <p>多选题</p>
                <hr class="my-2">
                <!--                选项-->
                <div class="my-3">
                    <input type="checkbox" id="a" class="form-check-input" value="ASP" v-model="answer"/>
                    <label for="a"> ASP</label>
                    <div></div>
                    <input type="checkbox" id="b" class="form-check-input" value="PHP" v-model="answer"/>
                    <label for="b"> PHP</label>
                    <div></div>
                    <input type="checkbox" id="c" class="form-check-input" value="HTML" v-model="answer"/>
                    <label for="c">HTML</label>
                    <div></div>
                    <input type="checkbox" id="d" class="form-check-input" value="JSP" v-model="answer"/>
                    <label for="d"> JSP</label>
                    <div></div>
                </div>

                <!--按钮-->
                <div class="row">
                    <div class="col-lg-6 col-xm-12">
                        <button @click="pre()" type="button" class="btn btn-danger btn-lg btn-block">
                            上一题
                        </button>
                    </div>
                    <div class="col-lg-6 col-xm-12">
                        <button @click="next()" type="button" class="btn btn-danger btn-lg btn-block">
                            下一题
                        </button>
                    </div>
                </div>



            </div>
        </div>



    </div>

</template>

<script>

  export default {

    name: "Third",
    data(){
      return{
        answer:[],
        message:null
      }

    },
    created() {

      this.answer=this.$store.state.three;
      this.$store.commit("mul");
      this.message=this.$store.state.count;
    },
    methods:{
      next(){


        this.$store.commit("threefunction",this.answer);
       // alert("您的选择为："+this.$store.state.three);
        this.$router.push("/forth");

      },
      pre(){

        this.$store.commit("threefunction",this.answer);
        this.$router.push("/second");
      }

    },



  }
</script>

