import Vue from 'vue'
import VueRouter from 'vue-router'
import First from '../views/First'
import Second from "../views/Second";
import Third from "../views/Third";
import Forth from "../views/Forth";
import Fifth from "../views/Fifth";
import Judge from "../views/judge"

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'first',
    component: First
  },
  {
    path: '/second',
    name: 'second',
    component: Second
  },
  {
    path: '/third',
    name: 'third',
    component: Third
  },
  {
    path: '/forth',
    name: 'forth',
    component: Forth
  },
  {
    path: '/fifth',
    name: 'fifth',
    component: Fifth
  },
  {
    path: '/judge',
    name: 'judge',
    component: Judge
  },
]

const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
