export default {
  mul(state){
    let count=0;
    if(state.one!=null){
      count++;
    }
    if(state.two[0]!=null){
      count++;
    }
    if(state.three[0]!=null){
     count++;
    }
    if(state.four!=null){
      count++;
    } if(state.five!=""){
      count++;
    }

    state.count=count;
  },
   onefunction(state,v){
    state.one=v
  },
  twofunction(state,v){
    state.two=v
  },
  threefunction(state,v){
    state.three=v
  },
  fourfunction(state,v){
    state.four=v
  },
  fivefunction(state,v){
    state.five=v
  },
}