<template>
    <div>
<!--        进度条-->
        <div class="progress">
            <div class="progress-bar progress-bar-striped bg-warning" role="progressbar"
                 v-bind:style="{width: (message*100/5)+'%'}" aria-valuenow="10" aria-valuemin="0" aria-valuemax="100">
            </div>
         </div>

        <div class="jumbotron jumbotron-fluid mt-3">
            <div class="container">
                <h1 class="display-4" >二，下列哪些是偶数？</h1>

                <p>多选题</p>
                <hr class="my-2">
<!--                选项-->
                <div class="my-3">
                    <input type="checkbox" class="form-check-input" id="a" value=2 v-model="answer"/>
                    <label for="a"> 2</label>
                    <div></div>
                    <input type="checkbox" class="form-check-input" id="b" value=3 v-model="answer"/>
                    <label for="b"> 3</label>
                    <div></div>
                    <input type="checkbox" class="form-check-input" id="c" value=4 v-model="answer"/>
                    <label for="c"> 4</label>
                    <div></div>
                    <input type="checkbox"  class="form-check-input" id="d" value=5 v-model="answer"/>
                    <label for="d"> 5</label>
                    <div></div>
                </div>

                <!--按钮-->
                <div class="row">
                    <div class="col-lg-6 col-xm-12">
                        <button @click="pre()" type="button" class="btn btn-danger btn-lg btn-block">
                            上一题
                        </button>
                    </div>
                    <div class="col-lg-6 col-xm-12">
                        <button @click="next()" type="button" class="btn btn-danger btn-lg btn-block">
                            下一题
                        </button>
                    </div>
                </div>



            </div>
        </div>



    </div>

</template>

<script>

  export default {

    name: "Second",
    data(){
         return{
           answer:[],
           message:null
    }

    },
    created() {

      this.answer=this.$store.state.two;
      this.$store.commit("mul");
      this.message=this.$store.state.count;
    },
    methods:{
      next(){


        this.$store.commit("twofunction",this.answer);
        //alert("您的选择为："+this.$store.state.two);
        this.$router.push("/third");

      },
      pre(){

        this.$store.commit("twofunction",this.answer);
        this.$router.push("/");
      }

    },



  }
</script>

