<template>
    <div>
        <!--        进度条-->
        <div class="progress">
            <div class="progress-bar progress-bar-striped bg-warning" role="progressbar"
                 v-bind:style="{width: (message*100/5)+'%'}" aria-valuenow="10" aria-valuemin="0" aria-valuemax="100">
            </div>
        </div>

        <div class="jumbotron jumbotron-fluid mt-3">
            <div class="container">
                <h1 class="display-4" >五、在服务端用来接受用户请求对象的是？</h1>

                <p>简答题</p>
                <hr class="my-2">
                <!--                选项-->
                <div class="my-3">
                    <textarea v-model="answer" placeholder="在这里输入内容....." style="width:350px;height: 120px"></textarea>
                </div>

                <!--按钮-->
                <div class="row">
                    <div class="col-lg-6 col-xm-12">
                        <button @click="pre()" type="button" class="btn btn-danger btn-lg btn-block">
                            上一题
                        </button>
                    </div>
                    <div class="col-lg-6 col-xm-12">
                        <button @click="next()" type="button" class="btn btn-danger btn-lg btn-block">
                            提交
                        </button>
                    </div>
                </div>



            </div>
        </div>



    </div>

</template>

<script>

  export default {

    name: "Forth",
    data(){
      return{
        answer:'',
        message:null
      }

    },
    created() {

      this.answer=this.$store.state.five;
      this.$store.commit("mul");
      this.message=this.$store.state.count;
    },
    methods:{
      next(){
        this.$store.commit("fivefunction",this.answer);
        this.$store.commit("mul");
        let number=this.$store.state.count;
        let el=5-number;

        if(number==5){
          alert("提交成功");
          this.$router.push("/judge");
        }
        else{
          alert("您还有"+el+"道题没有答！")
        }

      },
      pre(){

        this.$store.commit("fivefunction",this.answer);
        this.$router.push("/forth");
      }

    },



  }
</script>

