<template>
    <div>
        <div class="jumbotron jumbotron-fluid mt-3">
            <div class="container">
                <h1 class="display-4" > 您的得分为：{{message}}分</h1>
            </div>
            <hr class="my-2">
            <div class="my-3">
                <span>
                    一，2+3={{this.$store.state.trueone}}
                    ------您的回答为:{{this.$store.state.one}}------{{bijiao.noone}}
                </span>
                <hr class="my-2">
                <span>
                    二，偶数有:{{this.$store.state.truetwo}}
                    ------您的回答为:{{towint}}------{{bijiao.notwo}}
                </span>
                <hr class="my-2">
                <span>
                    三，动态网页有:{{this.$store.state.truethree}}
                    ------您的回答为:{{this.$store.state.three}}------{{bijiao.nothree}}
                </span>
                <hr class="my-2">
                <span>
                    四，服务器端的组件有:{{this.$store.state.truefour}}
                    ------您的回答为:{{this.$store.state.four}}------{{bijiao.nofour}}
                </span>
                <hr class="my-2">
                <span>
                    五，在服务端用来接受用户请求的对象是:{{this.$store.state.truefive}}
                    ------您的回答为:{{this.$store.state.five}}------{{bijiao.nofive}}
                </span>

            </div>
        </div>

    </div>
</template>

<script>
  export default {
    name: "judge",
    data(){
      return{
        message:'',
        towint:[],
        bijiao:{
          noone:'错误',
          notwo:'错误',
          nothree:'错误',
          nofour:'错误',
          nofive:'错误'
        }
      }
    },
    created() {

//将第二题的字符数组，转换为数字数值
      for(let i=0;i<this.$store.state.two.length;i++)
      {
        this.towint.push(parseInt(this.$store.state.two[i]));
      }




//全局变量
      let score=0;
     // alert(this.$store.state.one);
      //alert(this.$store.state.trueone)
      if(parseInt(this.$store.state.one)===this.$store.state.trueone){
        this.bijiao.noone='正确'
        score+=20;
      }


      //第二题判断
      //alert(this.$store.state.two)
      //先对答案排序
      this.$store.state.two.sort();
      this.$store.state.truetwo.sort();
      //alert(this.$store.state.two)
      //alert(this.$store.state.truetwo)
      //为0表示不同
      let b=1;
      //如果大小不相等
      if(this.$store.state.two.length!==this.$store.state.truetwo.length)
      {
        b=0;
      }
      //一个个比较
      for(let j=0;j<this.$store.state.two.length;j++)
      {
        if(parseInt(this.$store.state.two[j])!==this.$store.state.truetwo[j])
        {
          b=0;
          break;
        }
      }
      if(b===1){
        this.bijiao.notwo='正确'
        score+=20;
      }






      //第三题判断
      //alert(this.$store.state.three)
      //先对答案排序
      this.$store.state.three.sort();
      this.$store.state.truethree.sort();
      //alert(this.$store.state.three)
      //为0表示不同
      let c=1;
      //如果大小不相等
      if(this.$store.state.three.length!==this.$store.state.truethree.length)
      {
       c=0;
      }
      //一个个比较
      for(let i=0;i<this.$store.state.three.length;i++)
      {
        if(this.$store.state.three[i]!==this.$store.state.truethree[i])
        {
          c=0;
          break;
        }
      }
      if(c===1){
        this.bijiao.nothree='正确'
        score+=20;
      }

      //第四题
      if(this.$store.state.four===this.$store.state.truefour){
        this.bijiao.nofour='正确'
        score+=20;
      }



      //第五题
      if(this.$store.state.five.toLowerCase()===this.$store.state.truefive.toLowerCase()){
        this.bijiao.nofive='正确'
        score+=20;
      }




      this.message=score;


    }
  }
</script>

